ul.latest-posts
  list-style-type: none
  margin-block-start: 0
  margin-block-end: 0
  padding-inline-start: 0

  li
    display: grid
    grid-template-columns: 1fr 6fr
    column-gap: 1rem

    div.post-container
      display: inline-block

      div.title
        h2
          margin-bottom: 0.5rem

        h3
          margin-top: 0.5rem
          font-style: italic
          font-weight: 100
          font-size: 1rem

      h2.date
        font-weight: 100
        color: slategrey

      blockquote
        line-height: 1.8rem
        margin-inline-start: 1rem
        margin-inline-end: 0
        padding-left: 1rem
        border-left: 3px solid black
        font-style: italic

article.blog
  div.title
    h1
      margin-bottom: 0.5rem

    h2
      margin-top: 0.5rem
      font-style: italic
      font-weight: 100
      font-size: 1rem

  p.date
    margin-bottom: 0

  p.rep-materials
    margin-top: 0

  figure
    box-sizing: border-box
    display: inline-block
    position: relative
    left: 50%
    transform: translateX(-50%)
    margin: 0
    padding: 1rem
    max-width: 100%

    p
      margin-top: 0

    figcaption
      text-align: center
      font-style: italic

    img
      box-sizing: border-box
      display: block
      max-width: 100%
      margin: 0 auto

  figure.cover
    border: none
    width: auto
    padding: 0
    max-width: 100%

    img
      background: none
      max-width: 100%

    figcaption
      text-align: right
      font-size: 0.66rem
      margin-top: 1rem
      margin-left: auto
      max-width: 60%

  sup.multiple-fn::before
    content: ","

  div.footnotes::before
    content: "————————"
    height: 0

  sup[role='doc-noteref']
    margin-top: -4.22rem
    padding-top: 4.22rem

  li[role='doc-endnote']
    margin-top: -3.22rem
    padding-top: 3.22rem

  blockquote
    line-height: 1.8rem
    margin-inline-start: 1rem
    margin-inline-end: 0
    padding-left: 1rem
    border-left: 3px solid black
    font-style: italic

  figure.highlight
    width: 100%
    background: gainsboro
    border: none

    pre
      margin: 0

  /* Based on Pygments Murphy theme: https://pygments.org */

  /* Highlighting */
  .highlight .hll 
    background-color: #ffffcc 

  /* Comment */
  .highlight .c 
    color: #606060
    font-style: italic  

  /* Error */
  .highlight .err 
    color: #F00000
    background-color: #F0A0A0  

