figure.highlight
  box-sizing: border-box
  padding: 1rem
  background: gainsboro
  border: none

  pre
    margin: 0

/* Based on Pygments Murphy theme: https://pygments.org */

/* Highlighting */
.highlight .hll 
  background-color: #ffffcc 

/* Comment */
.highlight .c 
  color: #606060
  font-style: italic  

/* Error */
.highlight .err 
  color: #F00000
  background-color: #F0A0A0  

/* Keyword */
.highlight .k 
  color: #208090
  font-weight: bold  

/* Operator */
.highlight .o 
  color: #303030  

/* Comment.Multiline */
.highlight .cm 
  color: #606060
  font-style: italic  

/* Comment.Preproc */
.highlight .cp 
  color: #507090  

/* Comment.Single */
.highlight .c1 
  color: #606060
  font-style: italic  

/* Comment.Special */
.highlight .cs 
  color: #c00000
  font-weight: bold
  font-style: italic  

/* Generic.Deleted */
.highlight .gd 
  color: #A00000  

/* Generic.Emph */
.highlight .ge 
