// Site-wide styling
html
  margin-left: calc(100vw - 100%)
  margin-right: 0

body 
  max-width: calc(80ch + 2rem)
  margin: 0 auto 0 auto
  font-family: Palatino, Georgia, Serif
  min-height: 100vh
  display: grid
  grid-template-rows: auto auto 1fr auto
  background-color: ghostwhite

  a
    color: darkslategrey

header
  margin-bottom: 1rem

  h1
    margin: 4rem 0 0 0
    font-weight: normal
    font-size: 3rem

  .contact-info
    margin: 0

nav
  position: sticky
  z-index: 1
  top: 0
  background: ghostwhite
  padding: 1rem 0 0 0
  margin-bottom: 1rem
  .wrapper-nav-items
    left: 0
    right: 0
    background: ghostwhite
    padding: 0.5rem
    display: flex
    align-items: center
    justify-content: space-around
    border-top: 3px double
    border-bottom: 3px double

    a
      text-decoration: none
      color: black
      padding: 0.11rem 0.33rem
      
    a:hover
      background-color: gainsboro

    a.current
      font-weight: bold

main
  p
    line-height: 1.8rem
  li
    line-height: 1.8rem
  h1
    font-size: 1.5em
  h2
    font-size: 1.17em
  h3
    font-size: 1em

footer
  flex-shrink: 0
  width: 100%
  text-align: center
  font-size: 0.75rem
  margin: 2rem 0 1rem 0

sup
  line-height: 0

// Styling for specific pages
@import 'home'
@import 'paper'
@import 'blog'
@import 'code'
